.menu {
  overflow: auto;
  box-sizing: border-box;
  height: 100vh;
  background-color: #f9f9f9;
  font-weight: 700;

  &__top {
    position: sticky;
    top: 0;
    z-index: 1;
    background-color: #e5e5e5;
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__bottom {
    margin-bottom: 32px;

    @include on-tablet {
      margin-bottom: 56px;
    }
  }

  &__nav {
    margin-bottom: 48px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;

    font-size: 16px;
    letter-spacing: 2px;
    text-decoration: none;
    color: inherit;
  }

  &__call-to-order {
    display: block;
    width: fit-content;

    padding-bottom: 4px;
    border-bottom: 1px solid $main-text-color;

    font-size: 16px;
    letter-spacing: 2px;
    text-decoration: none;
    text-transform: uppercase;
    color: inherit;
  }
}
